/* Bar state colors */
.bar {
    background-color: #3b82f6; /* Blue-500 */
    border-radius: 6px 6px 0 0;
    margin: 0 2px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    transition: all 0.2s ease;
}
.bar.comparing { background-color: #f59e0b !important; } /* Amber */
.bar.swapping { background-color: #ef4444 !important; }  /* Red */
.bar.sorted { background-color: #22c55e !important; }    /* Green */

html {
    scroll-behavior: smooth;
}